@import "./font.scss";

*{
    padding: 0;
    margin: 0;
}

$primary-color: rgb(255, 250, 238);
$secondary-color: rgb(218, 224, 175);
$contrast-color: rgb(89, 90, 159);
$contrast-color2: rgb(118,65,61);
$font-color: #141414;
$font-color2: rgb(215,220,178);

body{
    background-color: $primary-color;
    .header{
        background-color: $secondary-color;
        display: flex;
        padding: 1rem;
        align-items: center;
        justify-content: space-between;  
        h1{
            width: 20%;
        } 
    }

    h1{
        font-family: "josefin_sans";
        font-size: 2rem;
    }

    p{
    font-family: "noman";
    font-size: 1rem;
    margin-bottom: 0.5rem;
}
}

.headline{
    font-family: "josefin_sans"; 
}

.navBar{
    ul{
        display: flex;
        list-style:none;
        justify-content: space-between;
        li{
            margin-left: 1rem;
            &:hover{
                color: white;
            }
        }
    }

    .container{
        border: solid black 1px;
        
    }
}

#canvas{
    touch-action: none !important;
}

#brushSize,#brushDensity{
    width: 1.6rem;
}
#stripe{
    border: solid black 3px;
}

.pattern1{
    // background: repeating-linear-gradient(36deg, rgba(255,255,255, 0.1), rgba(255,255,255, 0.1) 1px, transparent 0px, transparent 2px) !important;
}

.pattern2 {
    // background-image: repeating-linear-gradient(-45deg, rgba(255,255,255, 0.1), rgba(255,255,255, 0.1) 1px, transparent 1px, transparent 6px);
    background-size: 4px 4px;
}

.swiper {
    width: 974px;
  }
  .swiper-slide {
    height: 300px;
    
    line-height: 300px;
    text-align: center;
  }
    #top10 {
    min-width: 630px;
  }

  .footer  {
    background-color: $secondary-color;
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;    
    bottom: 0px;
  }

.App{
    min-height: 100vh;
    flex-direction: column;
}